    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body {
    height: 100vh;
    width: 100vw;
    display: flex;
}

body::-webkit-scrollbar {
    display: none;
}

.sidebar {
    min-width: 200px;
    flex: .5;
    background-color: rgb(209, 50, 50);
    padding: 2rem;
    box-shadow: 0px 0px 5px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.sidebar-title {
    margin: 0 auto;
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
    color: white;
}

.projects-list {
    color: white;
    display: flex;
    flex: 1;
    width: 100%;
    flex-direction: column;
    list-style-type: none;
    gap: .1rem;
    font-size: 1.25rem;
}

.project-save-label {
    width: 100%;
    transition: background-color .05s;
    border-radius: 4px;
    padding: 3px 6px;
    user-select: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
}


.project-save-label:hover {
    background-color: rgb(138, 30, 30);  
}

.project-save-label:active {
    background-color: rgba(138, 30, 30, .5);
}

.project-save {
    white-space: nowrap;
    max-width: 85%;
    overflow-x: scroll;
    scrollbar-width: none;
    -ms-overflow-style: none;
}


.delete-project-button {
    border: none;
    background: none;
    color: white;
}

.add-project-button {
    padding: .5rem 1.25rem;
    font-size: .75rem;
    font-weight: bold;
}

.add-project-dialog, .add-todo-item-dialog, .edit-todo-item-dialog {
    padding: 1.5rem;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
    border: 1px solid black;
    border-radius: .5rem;
}

.add-project-dialog > form, .add-todo-item-dialog > form, .edit-todo-item-dialog > form {
    display: flex;
    flex-direction: column;
    gap: 1.15rem;
}

.add-project-dialog .dialog-title, .add-todo-item-dialog .dialog-title, .edit-todo-item-dialog .dialog-title{
    font-size: 1.5rem;
    font-weight: bold;
}

.new-project-properties, .new-todo-item-properties, .edit-todo-item-properties {
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

.new-project-properties > *, .new-todo-item-properties > *, .edit-todo-item-properties > * {
    display: flex;
    justify-content: space-between;
    gap: 1rem;
}

.new-project-properties input, .new-todo-item-properties input, .new-todo-item-properties select, .edit-todo-item-properties input, .edit-todo-item-properties select{
    padding: 1px 5px;
    width: 200px;
}

.new-project-buttons-container, .new-todo-item-buttons-container, .edit-todo-item-buttons-container {
    display: flex;
    gap: .5rem;
    align-self: flex-end;
}

.new-project-buttons-container > *, .new-todo-item-buttons-container > *, .edit-todo-item-buttons-container > * {
    padding: 1px 5px;
}

.main-container {
    min-width: 500px;
    flex: 3;
    display: flex;
    flex-direction: column;
}

.dashboard {
    background-color: rgba(211, 211, 211, 0.25);
    height: 10rem;
    display: flex;
    padding: 1.5rem;
    gap: 1.5rem;
    justify-content: space-between;
    box-shadow: 0px 0px 5px;
    z-index: 1;
}

.project-information {
    min-width: 400px;
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: .15rem;
}

.project-title {
    white-space: nowrap;
    overflow-x: scroll;
    scrollbar-width: none;
    -ms-overflow-style: none;
    font-size: 2.5rem;
    font-weight: bold;
}

.project-description {
    white-space: nowrap;
    overflow-x: scroll;
    scrollbar-width: none;
    -ms-overflow-style: none;
    color: rgb(100, 100, 100);
}

.add-todo-item-button {
    align-self: flex-end;
    padding: .5rem 2.5rem;
    font-size: 1rem;
    font-weight: bold;
}

.main-window {
    overflow: scroll;
    scrollbar-width: none;
    -ms-overflow-style: none;
    display: flex;
    padding: 2rem;
    gap: 2rem;
    flex-direction: column;
}

.todo-item {
    border-radius: .5rem;
    padding: 1rem;
    box-shadow: 0px 0px 2px .01px gray;
    display: flex;
    justify-content: space-between;
    max-height: 250px;
    width: 100%;
}

option[selected] {
    display: none;
}

.high-priority {
    background-color: rgb(240, 248, 255);
}

.medium-priority {
    background-color: rgb(255, 240, 242);
}

.low-priority {
    background-color: rgb(255, 255, 230);
}

.todo-item-information {
    width: 500px;
}

.todo-item-title {
    font-size: 2.25rem;
    font-weight: bold;
}

.todo-item-properties {
    display: flex;
    gap: 2rem;
    max-height: 50px;
    overflow: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.todo-item-properties > * {
    flex: 1;
}

.todo-item-buttons-container {
    display: flex;
    gap: .5rem;
    justify-content: flex-end;
    align-items: flex-end;
}

.todo-item-buttons-container > * {
    font-size: 1rem;
    padding: .25rem 1.25rem;
